import React from 'react';
import { Container, TabContent, TabPane, Row, Col, Nav, NavItem, NavLink } from "reactstrap";
import classnames from 'classnames';

class Project extends React.Component {
    constructor(props) {
      super(props);
  
      this.toggle = this.toggle.bind(this);
      this.state = {
        activeTab: '1'
      };
    }
  
    toggle(tab) {
      if (this.state.activeTab !== tab) {
        this.setState({
          activeTab: tab
        });
      }
    }
  
    render() {
      return <div>
        <Container>
          <Nav tabs>
            <NavItem>
              <NavLink className={classnames({ active: this.state.activeTab === '1' })}
                       onClick={() => { this.toggle('1'); }}>
                Fanorona
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className={classnames({ active: this.state.activeTab === '2' })}
                       onClick={() => { this.toggle('2'); }}>
                Madagascar Election
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink className={classnames({ active: this.state.activeTab === '3' })}
                       onClick={() => { this.toggle('3'); }}>
                Family Tree
              </NavLink>
            </NavItem>
          </Nav>
          <TabContent activeTab={this.state.activeTab}>
            <TabPane tabId="1">
            <Fanorona/>
            </TabPane>
            <TabPane tabId="2">
              <Madastat/>
            </TabPane>
            <TabPane tabId="3">
              <br></br>
              <p>
                  See : <a href="https://rajoelina.pythonanywhere.com">https://rajoelina.pythonanywhere.com</a>
              </p>
              <MyFamilytree/>
              <p style={{paddingBottom:"200px"}}></p>
            </TabPane>          
          </TabContent>
        </Container>
      </div>;
    }
  }

class Iframes extends React.Component {
render() {
    return(         
        <div>          
        <iframe title={this.props.title} height={this.props.height} width={this.props.width} src={this.props.src} />          
        </div>
    );
    }
}

class Fanorona extends React.Component {
    render() {
    return (
    <Container>
        <Row style={{margin:"25px 0px"}}>
        <Col col-12="true">
            <Iframes src="https://fanoronaraf.pythonanywhere.com" title="frameP1" width="100%" height="670px"/>
        </Col>
        </Row>
    </Container>
    )
    }
} 

