    render() {
        return (
        <Container>
        <Row style={{margin:"25px 0px"}}>
            <Col col-12="true">
            <Iframes src="https://madastat.pythonanywhere.com" title="frameP2" width="100%" height="670px"/>
            </Col>
        </Row>
        </Container>
        )
    }
