class MyFamilytree extends React.Component {
    render() {
        return (
        <Container>
        <Row style={{margin:"25px 0px"}}>
            <Col col-12="true">
            <Iframes src="https://rajoelina.pythonanywhere.com" title="frameP3" width="100%" height="670px"/>
            </Col>
        </Row>
        </Container>
        )
    }
}    


export default Project;